.card {
  border: 4px #636363 solid;
  background: #2727fc;
  border-radius: 2px;
  width: 150px;
  height: 150px;
  margin: 1rem;
  position: relative;
  box-shadow: 4px 4px 10px #383838;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  background:#ffffff;
}

.card > .img-container > img {
  height: 100%;
}

.card:hover {
  box-shadow: 8px 8px 20px #383838;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card:active {
  -webkit-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}
nav {
  width: 100%;
  font-size: 3rem;
  color: #ffffff;
  margin-bottom: 50px;
  background: #2c2c2c;
  height: auto;
  font-family: 'Bree Serif', serif;
}

a {
  color: #fed420;
  font-family: 'Bree Serif', serif;
}

a:hover {
  color: #04d31f;
  text-decoration: none;
}

nav li {
  display: inline-block;
  padding: 26px 0;
  width: 25%;
  font-family: 'Bree Serif', serif;
  /* text-align: center; */
}

ul {
  /* padding-left: 0px; */
  display: flex;
  align-items: center;
  font-family: 'Bree Serif', serif;
  background: #2c2c2c;
}

@media only screen and (max-width: 750px) {
  #cur-sco, #top-sco {
    font-size: 2rem;
  }
}
.wrapper {
  background: #1919fc;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  padding-bottom: 50px;
  font-family: 'Bree Serif', serif;
}
.title {
  width: 100%;
  text-align: right;
  font-size: 2rem;
  color: #444;
  margin-bottom: 50px;
  padding-left: 3%;
  padding-right: 3%;  
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
  font-family: 'Bree Serif', serif;
  background: #8f8ff3;
}
* {
  box-sizing: border-box;
  font-family: 'Bungee', cursive;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
